<template>
	<div>
		<div class="ace-settings-item">
			<span>Choose Skin</span>
			<select id="skin-colorpicker" @change="func_skin_colorpicker">
				<option data-skin="" value="#438EB9">-</option>
				<option data-skin="no-skin" value="#438EB9">no-skin</option>
				<option data-skin="skin-1" value="#222A2D">skin-1</option>
				<option data-skin="skin-2" value="#C6487E">skin-2</option>
				<option data-skin="skin-3" value="#D0D0D0">skin-3</option>
			</select>
		</div>
		<br>

		<div class="ace-settings-item">
			<input type="checkbox" class="ace ace-checkbox-2 ace-save-state" id="ace-settings-navbar" autocomplete="off" @click="func_ace_settings_navbar"/>
			<label class="lbl" for="ace-settings-navbar"> Fixed Navbar</label>
		</div>

		<div class="ace-settings-item">
			<input type="checkbox" class="ace ace-checkbox-2 ace-save-state" id="ace-settings-sidebar" autocomplete="off" @click="func_ace_settings_sidebar"/>
			<label class="lbl" for="ace-settings-sidebar"> Fixed Sidebar</label>
		</div>

		<div class="ace-settings-item">
			<input type="checkbox" class="ace ace-checkbox-2 ace-save-state" id="ace-settings-breadcrumbs" autocomplete="off" @click="func_ace_settings_breadcrumbs"/>
			<label class="lbl" for="ace-settings-breadcrumbs"> Fixed Breadcrumbs</label>
		</div>

		<div class="ace-settings-item">
			<input type="checkbox" class="ace ace-checkbox-2 ace-save-state" id="ace-settings-add-container" autocomplete="off" @click="func_ace_settings_add_container"/>
			<label class="lbl" for="ace-settings-add-container">
				Inside
				<b>.container</b>
			</label>
		</div>

		<div class="ace-settings-item">
			<input type="checkbox" class="ace ace-checkbox-2" id="ace-settings-hover" autocomplete="off" @click="func_ace_settings_hover"/>
			<label class="lbl" for="ace-settings-hover"> Submenu on Hover</label>
		</div>

		<div class="ace-settings-item">
			<input type="checkbox" class="ace ace-checkbox-2" id="ace-settings-compact" autocomplete="off" @click="func_ace_settings_compact"/>
			<label class="lbl" for="ace-settings-compact"> Compact Sidebar</label>
		</div>

		<div class="ace-settings-item">
			<input type="checkbox" class="ace ace-checkbox-2" id="ace-settings-highlight" autocomplete="off" @click="func_ace_settings_highlight"/>
			<label class="lbl" for="ace-settings-highlight"> Alt. Active Item</label>
		</div>
	</div>
</template>

<script type="text/javascript">
	import _skin from '@/lib/skin.js'

	export default {
		methods: {
			func_skin_colorpicker: function (event) {
				var skin_class = $('#skin-colorpicker').find('option:selected').data('skin');
				if (skin_class != "") {
					_skin.save_setting(skin_class);
					_skin.load_setting();
				}
			},
			func_ace_settings_navbar: function (event) {
				ace.settingFunction.navbar_fixed(null, event.target.checked);
			},
			func_ace_settings_sidebar: function (event) {
				ace.settingFunction.sidebar_fixed(null, event.target.checked);
			},
			func_ace_settings_breadcrumbs: function (event) {
				ace.settingFunction.breadcrumbs_fixed(null, event.target.checked);
			},
			func_ace_settings_add_container: function (event) {
				ace.settingFunction.main_container_fixed(null, event.target.checked);
			},
			func_ace_settings_compact: function (event) {
				if(event.target.checked) {
					$('#sidebar').addClass('compact');
					var hover = $('#ace-settings-hover');
					if( hover.length > 0 ) {
						hover.removeAttr('checked').trigger('click');
					}
				} else {
					$('#sidebar').removeClass('compact');
					$('#sidebar[data-sidebar-scroll=true]').ace_sidebar_scroll('reset')
				}
				
				if(ace.vars['old_ie']) ace.helper.redraw($('#sidebar')[0], true);
			},
			func_ace_settings_highlight: function (event) {
				if(event.target.checked) $('#sidebar .nav-list > li').addClass('highlight');
				else $('#sidebar .nav-list > li').removeClass('highlight');
				
				if(ace.vars['old_ie']) ace.helper.redraw($('#sidebar')[0]);
			},
			func_ace_settings_hover: function (event) {
				if($('#sidebar').hasClass('h-sidebar')) return;
				if(event.target.checked) {
					$('#sidebar li').addClass('hover')
					.filter('.open').removeClass('open').find('> .submenu').css('display', 'none');
					//and remove .open items
				}
				else {
					$('#sidebar li.hover').removeClass('hover');
					var compact = $('#ace-settings-compact');
					if( compact.length > 0 && compact.get(0).checked ) {
						compact.trigger('click');
					}
				}
				
				$('.sidebar[data-sidebar-hover=true]').ace_sidebar_hover('reset')
				$('.sidebar[data-sidebar-scroll=true]').ace_sidebar_scroll('reset')
				
				if(ace.vars['old_ie']) ace.helper.redraw($('#sidebar')[0]);
			}
		},
	}
</script>

<style>

</style>



